import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Image from "../components/image"
import "../css/components/footer.scss"

let images = [
  {
    url: "logos/vitamin_games_color_logo.png",
    bg: false
  },
  {
    url: "logos/vitamin_games_gb_logo.png",
    bg: true
  },
  {
    url: "logos/vitamin_games_bw_logo.png",
    bg: true
  }
]

let imgTmp = images[Math.floor(Math.random() * images.length)];

const Footer = ({ siteTitle, staticTheme }) => (
  <footer className={"page " + ((((staticTheme != null) ? images[staticTheme].bg : imgTmp.bg)) ? "bg" : "")}>
    <div className="wrapper">
      <div className="left">
        <Link to="/">
          <Image filename={(staticTheme != null) ? images[staticTheme].url : imgTmp.url}/>
        </Link>
      </div>
      <div className="right">
        <p>Healthy Fun <span>(◕‿◕✿)</span></p>
      </div>
    </div>
  </footer>
)

Footer.propTypes = {
  siteTitle: PropTypes.string,
}

Footer.defaultProps = {
  siteTitle: ``,
  staticTheme: null
}

export default Footer
