/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql } from "gatsby"

import Header from "./header"
import Footer from "./footer"
import "../css/main.scss"

const Layout = (props) => (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
      }
    `}
    render={data => (
      <>
        {(props.showHeader) ?
          <Header staticTheme={props.staticTheme} siteTitle={data.site.siteMetadata.title} />
        :null}
        <main className={((props.showFooter) ? "" : "no-footer ") + ((props.showHeader) ? "" : "no-header ")}>{props.children}</main>
        {(props.showFooter) ?
          <Footer />
        :null}
      </>
    )}
  />
)


Layout.defaultProps = {
  showHeader: true,
  showFooter: true,
  staticTheme: null,
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
