import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Image from "../components/image"
import "../css/components/header.scss"

let images = [
  {
    url: "logos/vitamin_games_color_logo.png",
    bg: false
  },
  {
    url: "logos/vitamin_games_gb_logo.png",
    bg: true
  },
  {
    url: "logos/vitamin_games_bw_logo.png",
    bg: true
  }
]

let imgTmp = images[Math.floor(Math.random() * images.length)];

const Header = ({ siteTitle, staticTheme }) => (
  <header className={"page " + ((((staticTheme != null) ? images[staticTheme].bg : imgTmp.bg)) ? "bg" : "")}>
    <div className="wrapper">
      <div className="left">
        <Link to="/">
          <Image filename={(staticTheme != null) ? images[staticTheme].url : imgTmp.url}/>
          <p>Vitamin Games</p>
        </Link>
      </div>
      <div className="right">
        <ul>
          <li><Link activeClassName={"active"} to="/">Home</Link></li>
          {/* <li><Link activeClassName={"active"} to="/games">Games</Link></li> */}
          {/* <li><Link activeClassName={"active"} to="/updates">Updates</Link></li> */}
          <li><Link activeClassName={"active"} to="/project-arrhythmia">Arrhythmia</Link></li>
          <li><Link activeClassName={"active"} to="/press">Press</Link></li>
          <li><Link activeClassName={"active"} to="/contact">Contact</Link></li>
        </ul>
      </div>
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
  staticTheme: null
}

export default Header
